$(function () {
    //鼠标经过
    $(".content li").mouseenter(function () {
        $(this).find(".box1").css("left", "-100%").siblings(".box2").css("left", "0")
    }).mouseleave(function () {
        $(this).find(".box1").css("left", "0").siblings(".box2").css("left", "-100%")
    });

    //切换
    $("#web,#other").click(function () {
        $(this).addClass("select").siblings().removeClass("select");
        $("." + $(this).prop("id")).css("display", "block").siblings().css("display", "none");
    })
})


