<template>
  <div class="contain">
    <div class="main">
      <div class="content">
        <div class="web">
          <ul>
            <li v-for="item,index in list1">
              <div class="box1">
                <router-link tag="a" :to="item.url">
                  <div class="web_workTime">{{item.time}}</div>
                  <h2 class="web_workName">{{item.name}}</h2>
                  <div class="web_workNum">{{index+1}}</div>
                </router-link>
              </div>
              <div class="box2">
                <router-link tag="a" :to="item.url">
                  <div class="web_workTime">{{item.time}}</div>
                  <h2 class="web_workName">{{item.name}}</h2>
                  <div class="web_workNum">{{index+1}}</div>
                </router-link>
              </div>
            </li>
          </ul>
        </div>
        <div class="other">
          <ul>
            <li v-for="item,index in list2">
              <div class="box1">
                <a href="javascript:;">
                  <div class="web_workTime">{{item.time}}</div>
                  <h2 class="web_workName">{{item.name}}</h2>
                  <div class="web_workNum">{{index+1}}</div>
                </a>
              </div>
              <div class="box2">
                <a href="javascript:;">
                  <div class="web_workTime">{{item.time}}</div>
                  <h2 class="web_workName">{{item.name}}</h2>
                  <div class="web_workNum">{{index+1}}</div>
                </a>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="directory">
        <div id="web" class="select"><span>web</span></div>
        <div id="other"><span>other</span></div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import "../assets/js/work";
export default {
  data() {
    //这里存放数据
    return {
      list1: [
        { name: "别踩白块", time: "2020.2.2", url: "/demo/bcbk" },
        { name: "仿小米商城静态网页", time: "2020.2.2", url: "/demo/mi/index" },
        { name: "test", time: "2020.2.2", url: "/demo/mi/index" },
        { name: "test", time: "2020.2.2", url: "/demo/mi/index" }
      ],
      list2: [
        { name: "一二三四", time: "2020.2.2" },
        { name: "一二三四", time: "2020.2.2" }
      ]
    };
  }
};
</script>
<style scoped>
@import "../assets/css/common.css";
@import "../assets/css/work.css";
</style>


